import React, { FC } from 'react';
import { PolygonProps } from './Polygon.types';
import { ngon, rad, Cubic, subtract, add } from '../../geometry';
import { Svg, Path } from '../Svg';
import { useNgon } from './Polygon.methods';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fill = '#000',
	stroke = '#000',
	strokeWidth = 0.1,
}) => {
	const [points, viewBox, scaledStrokeWidth] = useNgon(sides, scale, rotation, cornerRadius, strokeWidth);
	const scaledLineWidth = strokeWidth * scale;
	const hl = scaledLineWidth > 0 ? scaledLineWidth/2:0;
	return (<Svg 
	viewBox={viewBox}
	width='100px' 
	height='100px'>
		<Path {...{points, fill, stroke, strokeWidth:scaledStrokeWidth, scale}}/>
	</Svg>);
};